// 01. Color system

$white: #ffffff !default;
$gray-500: #617a86 !default;
$gray-100: mix(#fff, $gray-500, 60%) !default;
$gray-200: mix(#fff, $gray-500, 45%) !default;
$gray-300: mix(#fff, $gray-500, 30%) !default;
$gray-400: mix(#fff, $gray-500, 15%) !default;
$gray-500: #617a86 !default;
$gray-600: mix(#000, $gray-500, 15%) !default;
$gray-700: mix(#000, $gray-500, 30%) !default;
$gray-800: mix(#000, $gray-500, 45%) !default;
$gray-900: mix(#000, $gray-500, 60%) !default;
$yellow: #f9e80d !default;
$orange: #ff9f0c !default;
$aqua: #0cfff5 !default;
$blue: #0271ff !default;
$indigo: #5c25ff !default;
$purple: #b90dff !default;
$red: #e00000 !default;
$teal: #249d79 !default;
$green: #148922 !default;
$lime: #50b00a !default;
$pink: #e61079 !default;
$cyan: #009be3 !default;
$default: $gray-400 !default;
$dark: $gray-800 !default;
$black: #000000 !default;
$light: $gray-100 !default;
$primary: $blue !default;
$warning: $orange !default;
$danger: $red !default;
$success: $teal !default;
$info: $cyan !default;
$secondary: $gray-400 !default;
$text-muted: $gray-400 !default;
$inverse: $black !default;
$theme: $teal !default;
$theme-color: $white !default;
$theme-colors: (
  'default': $default,
  'theme': $theme,
  'theme-color': $theme-color,
  'primary': $primary,
  'secondary': $secondary,
  'warning': $warning,
  'danger': $danger,
  'success': $success,
  'inverse': $inverse,
  'info': $info,
  'dark': $dark,
  'black': $black,
  'white': $white,
  'purple': $purple,
  'indigo': $indigo,
  'red': $red,
  'pink': $pink,
  'green': $green,
  'yellow': $yellow,
  'teal': $teal,
  'muted': $gray-500,
  'light': $light,
  'lime': $lime,
  'gray-100': $gray-100,
  'gray-200': $gray-200,
  'gray-300': $gray-300,
  'gray-400': $gray-400,
  'gray-500': $gray-500,
  'gray-600': $gray-600,
  'gray-700': $gray-700,
  'gray-800': $gray-800,
  'gray-900': $gray-900,
);

$min-contrast-ratio: 3 !default;
$color-contrast-dark: $black !default;
$color-contrast-light: $white !default;

