.error-page {
  min-height: 100vh;
  padding: $spacer * 4 0;
  display: flex;
  align-items: center;

  & .error-page-content {
    margin: 0 auto;
    position: relative;
    padding: 0 $spacer;
    text-align: center;

    & .error-code {
      color: var(--#{$prefix}inverse);
      left: 0;
      right: 0;
      font-size: rem(118px);
      font-weight: $font-weight-bold;
      text-align: center;
      line-height: 1.25;
      letter-spacing: 5px;
      text-shadow: 10px 10px rgba(var(--#{$prefix}inverse-rgb), 0.15);

      @include media-breakpoint-down(sm) {
        font-size: rem(102px);
      }
    }
    & .error-page-icon i {
      font-size: rem(128px);
      color: $gray-300;
    }
    & h1 {
      font-size: rem(48px);
      font-weight: $font-weight-bold;

      @include media-breakpoint-down(sm) {
        font-size: rem(52px);
      }
    }
    & h3 {
      font-size: rem(18px);
      font-weight: 400;

      @include media-breakpoint-down(sm) {
        font-size: rem(16px);
      }
    }
    & .link-divider {
      width: rem(4px);
      height: rem(4px);
      display: inline-block;
      background: rgba(var(--#{$prefix}inverse-rgb), 0.3);
      position: relative;
      top: rem(-2px);
      margin: 0 $spacer;
      border-radius: $border-radius;
    }
  }
}
