.page-header {
  color: var(--#{$prefix}heading-color);
  padding: 0;
  border: none;
  margin-bottom: $spacer;
  font-weight: $font-weight-bold;
  font-size: rem(28px);

  & small {
    font-size: rem(16px);
    color: rgba(var(--#{$prefix}inverse-rgb), 0.5);
    font-weight: $font-weight-light;
  }
}
